<script>
import Navbar from "../components/Navbar.vue";

export default {
  name: "HomeView",
  data() {
    return {
      inMove: false,
      activeSection: 0,
      offsets: [],
      touchStartY: 0,
      isMobile: false,
      isMidMobile: false,
      cols: null,
      iconWidth: null,
      height: null,
      pad: false,
      heightWithoutFunctions: null,
      pcHeight: "35vh",
      pcCols: 3,
      usosCols: 2,
      usosWidth: "10vw",
      clientesCols: 3,
    };
  },
  components: {
    Navbar,
  },
  methods: {
    goToWsp1() {
      location.href =
        "https://api.whatsapp.com/send?phone=542612510987&text=Hola%21+Queria+consultar+por+el+servicio+de+localizaci%C3%B3n+vehicular&_fb_noscript=1";
    },
    goToWsp2() {
      location.href =
        "https://api.whatsapp.com/send?phone=542646090444&text=Hola%21+Queria+consultar+por+el+servicio+de+localizaci%C3%B3n+vehicular%0A&_fb_noscript=1";
    },
    goToMail() {
      location.href = "mailto:contacto@globaltracker.com.ar";
    },

    getResolution() {
      var height = window.innerHeight;
      var navbar = document.querySelector(".navbar");
      var rootStyles = getComputedStyle(navbar);
      var alturaConMugre = rootStyles.getPropertyValue("height");
      var altura = parseInt(alturaConMugre.slice(0, alturaConMugre.length - 2));
      var alturaDef = height - altura;
      this.height = alturaDef;

      var funcionesDelGps = document.querySelector(".funcionesDelGps");
      var rootStyles2 = getComputedStyle(funcionesDelGps);
      var alturaConMugre2 = rootStyles2.getPropertyValue("height");
      var altura2 = parseInt(
        alturaConMugre2.slice(0, alturaConMugre2.length - 2)
      );
      var alturaDef2 = height - altura - altura2;
      this.heightWithoutFunctions = alturaDef2;

      if (window.innerWidth <= 1500) {
        if (window.innerWidth <= 1100) {
          this.isMobile = true;
          this.isMidMobile = false;
          this.pcHeight = "35vh";
          this.pcCols = 2;
          this.usosCols = 3;
          this.clientesCols = 3;
          this.usosWidth = "16vw";
          this.cols = 6;
          this.pad = false;
          this.iconWidth = "iconWidthChico";
          if (window.innerWidth <= 850) {
            if (window.innerWidth <= 560) {
              this.cols = 12;
              this.pad = true;
              this.usosWidth = "22vw";
              this.usosCols = 6;
              this.iconWidth = "iconWidthUltraMegaChico";
            } else {
              this.pcHeight = "18vh";
              this.pcCols = 2;
              this.cols = 6;
              this.usosWidth = "19vw";
              this.usosCols = 3;
              this.pad = false;
              this.iconWidth = "iconWidthUltraChico";
            }
          }
        } else {
          this.isMidMobile = true;
          this.isMobile = false;
          this.pad = false;
          this.pcHeight = "35vh";
          this.usosWidth = "13vw";
          this.usosCols = 2;
          this.clientesCols = 3;
          this.pcCols = 3;
          this.cols = 3;
          this.iconWidth = "iconWidthGrande";
        }
      } else {
        this.isMobile = false;
        this.isMidMobile = false;
        this.pad = false;
        this.pcHeight = "35vh";
        this.usosWidth = "10vw";
        this.clientesCols = 3;
        this.pcCols = 3;
        this.usosCols = 2;
        this.cols = 2;
        this.iconWidth = "iconWidthGrande";
      }
    },

    calculateSectionOffsets() {
      if (this.pad) {
        this.getResolution();
        let sections = document.getElementsByTagName("section");
        let length = sections.length + 1;

        for (let i = 0; i < length; i++) {
          let sectionOffset = sections[i].offsetTop;
          this.offsets.push(sectionOffset);
        }
      } else {
        this.getResolution();
        let sections = document.getElementsByTagName("section");
        let length = sections.length;

        for (let i = 0; i < length; i++) {
          let sectionOffset = sections[i].offsetTop;
          this.offsets.push(sectionOffset);
        }
      }
    },
    handleMouseWheel: function (e) {
      if (e.wheelDelta < 10 && !this.inMove) {
        this.getResolution();
        this.moveUp();
      } else if (e.wheelDelta > 10 && !this.inMove) {
        this.getResolution();
        this.moveDown();
      }

      e.preventDefault();
      return false;
    },
    handleMouseWheelDOM: function (e) {
      if (e.detail > 0 && !this.inMove) {
        this.getResolution();
        this.moveUp();
      } else if (e.detail < 0 && !this.inMove) {
        this.getResolution();
        this.moveDown();
      }

      return false;
    },
    moveDown() {
      this.getResolution();
      this.inMove = true;
      this.activeSection--;

      if (this.activeSection < 0) this.activeSection = this.offsets.length - 1;

      this.scrollToSection(this.activeSection, true);
    },
    moveUp() {
      this.getResolution();
      this.inMove = true;
      this.activeSection++;

      if (this.activeSection > this.offsets.length - 1) this.activeSection = 0;

      this.scrollToSection(this.activeSection, true);
    },
    scrollToSection(id, force = false) {
      if (this.inMove && !force) return false;

      this.activeSection = id;
      this.inMove = true;

      document
        .getElementsByTagName("section")
        [id].scrollIntoView({ behavior: "smooth" });

      setTimeout(() => {
        this.inMove = false;
      }, 400);
    },
    touchStart(e) {
      e.preventDefault();

      this.touchStartY = e.touches[0].clientY;
    },
    touchMove(e) {
      if (this.inMove) return false;
      e.preventDefault();

      const currentY = e.touches[0].clientY;

      if (this.touchStartY < currentY) {
        this.moveDown();
      } else {
        this.moveUp();
      }

      this.touchStartY = 0;
      return false;
    },
  },
  render() {},
  mounted() {
    window.addEventListener("resize", this.getResolution); // mobile devices

    this.calculateSectionOffsets();

    window.addEventListener("DOMMouseScroll", this.handleMouseWheelDOM); // Mozilla Firefox
    window.addEventListener("mousewheel", this.handleMouseWheel, {
      passive: false,
    }); // Other browsers

    window.addEventListener("touchstart", this.touchStart, { passive: false }); // mobile devices
    window.addEventListener("touchmove", this.touchMove, { passive: false }); // mobile devices
  },
  destroyed() {
    window.removeEventListener("resize", this.getResolution);
    window.removeEventListener("mousewheel", this.handleMouseWheel, {
      passive: false,
    }); // Other browsers
    window.removeEventListener("DOMMouseScroll", this.handleMouseWheelDOM); // Mozilla Firefox

    window.removeEventListener("touchstart", this.touchStart); // mobile devices
    window.removeEventListener("touchmove", this.touchMove); // mobile devices
  },
};
</script>

