<template>
  <div class="navbar" style="padding: 10px">
    <img src="../assets/globalTrackerLogo.png" class="logo" />
    <div class="user" v-if="!isAppBarBtn">
      <button type="button" class="buttonNavbar" @click="whichSection(10)">
        Contacto
      </button>
      <v-menu
        offset-y
        open-on-hover
        close-delay="220"
        transition="scroll-y-transition"
      >
        <template v-slot:activator="{ on, attrs }">
          <button type="button" class="buttonNavbar" v-bind="attrs" v-on="on">
            Acceso Clientes
          </button>
        </template>
        <v-btn
          v-for="(tipo, i) in itemsClientes"
          :key="i"
          block
          plain
          class="d-flex flex-column"
          x-large
          style="
            height: 70px;
            background: rgba(86, 86, 86, 0.6);
            backdrop-filter: blur(10px);
            color: #ffffff;
          "
          @click="whichSection(i)"
          ><span style="font-size: 25px">{{ tipo.title }}</span>
        </v-btn>
      </v-menu>
    </div>
    <!-- @click="toggleisAccesoClientes" -->
    <div v-if="isAppBarBtn">
      <div class="text-right">
        <v-menu
          offset-y
          open-on-hover
          close-delay="220"
          class="rounded-lg"
          transition="scroll-y-transition"
          :close-on-content-click="false"
        >
          <template v-slot:activator="{ on, attrs }">
            <button dark v-bind="attrs" v-on="on">
              <v-icon dark right x-large color="black">mdi-menu</v-icon>
            </button>
          </template>

          <v-btn
            v-for="(item, index) in items"
            :key="index"
            block
            plain
            class="d-flex flex-column"
            x-large
            style="
              height: 70px;
              background: rgba(86, 86, 86, 0.6);
              backdrop-filter: blur(10px);
              color: #ffffff;
            "
            @click="toggleisAccesoClientes(index)"
            ><span style="font-size: 25px">{{ item.title }}</span>
          </v-btn>
          <div v-if="isAccesoClientes">
            <v-btn
              v-for="(tipo, i) in itemsClientes"
              :key="i"
              block
              plain
              class="d-flex flex-column"
              x-large
              style="
                height: 70px;
                background: rgba(86, 86, 86, 0.6);
                backdrop-filter: blur(10px);
                color: #ffffff;
              "
              @click="whichSection(i)"
              ><span style="font-size: 25px">{{ tipo.title }}</span>
            </v-btn>
          </div>
        </v-menu>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavBar",
  data() {
    return {
      isAppBarBtn: false,
      items: [{ title: "Contacto" }, { title: "Acceso Clientes" }],
      itemsClientes: [{ title: "Wan Way" }, { title: "Sino Tracking" }],
      isAccesoClientes: false,
    };
  },
  methods: {
    toggleisAccesoClientes(index) {
      if (index == 0) {
        this.$parent.scrollToSection(3, true);
        this.isAccesoClientes = false;
      } else {
        this.isAccesoClientes = true;
      }
    },
    getResolution() {
      if (window.innerWidth <= 1500) {
        this.isAppBarBtn = true;
        return true;
      } else {
        this.isAppBarBtn = false;
        return false;
      }
    },
    whichSection(index) {
      if (index == 10) {
        this.$parent.scrollToSection(3, true);
        this.isAccesoClientes = false;
      } else if (index == 0) {
        location.href = "https://www.wanwaygps.com/indexv2.html";
        this.isAccesoClientes = false;
      } else {
        location.href = "http://www.sinotracking.com/";
        this.isAccesoClientes = false;
      }
    },
  },
  mounted() {
    window.addEventListener("resize", this.getResolution); // mobile devices
    this.getResolution();
  },
  destroyed() {
    window.removeEventListener("resize", this.getResolution);
  },
};
</script>

